<template>
    <div>
        <div class="container pt-4 pb-2 my-4 border text-left" style="background-color:white; ">
            <h2>Object detection with Tensorflow.js</h2>
            <p>Below is a simple object detection model built using the pre-trained <a href="https://github.com/tensorflow/tfjs-models/tree/master/coco-ssd" target="_blank">coco-ssd</a>  model in Tensorflow.js. The model might take a few seconds to load. Works best on Safari or Firefox.</p>
            <div>
                <div v-if="webcamReady" class="video-container">
                    <video id="webcam2" ref="webcam2" autoplay muted playsinline width="640" height="480"></video>
                    <canvas id="canvas2" ref="canvas2" class="canvas"></canvas>
                </div>
                <div class="btn-toolbar">
                    <button :disabled="webcamReady" class="btn2 mr-2" v-on:click="showWebcam">SHOW</button>
                    <button :disabled="!webcamReady || webcamStarted" class="btn2 mr-2" v-on:click="startWebcam">START</button>
                    <button :disabled="!webcamStarted" class="btn2" v-on:click="stopWebcam">STOP</button>
                </div>
            </div>
        </div>
   </div>
</template>

<script>

import Webcam from 'webcam-easy';
import * as cocossd from '@tensorflow-models/coco-ssd'

  export default {
    data () {
        return {
            webcam: null,
            net: null,
            canvas: null,
            webcamReady: false,
            webcamStarted: false
        }
    },
    name: 'Object',
    methods: {
        showWebcam() {
            this.webcamReady = true;
        },
        stopWebcam() {
            window.location.reload()
        },
        startWebcam() {
            this.webcamStarted = true;
            this.initWebcam();
            this.runCoco()
        },
        async detectObj(net) {
            const obj = await net.detect(this.$refs.webcam2)
            const canvas = document.getElementById('canvas2')
            canvas.width = this.$refs.webcam2.width
            canvas.height = this.$refs.webcam2.height
            const ctx = canvas.getContext("2d");
            this.drawRect(obj, ctx)
        },
        initWebcam() {
            this.webcam = new Webcam(this.$refs.webcam2, 'user', document.getElementById('canvas'))
            this.webcam.start().then(() => {
                this.webcam.flip()
            });
        },
        async runCoco() {
            const net = await cocossd.load();
            console.log('model loaded')
            setInterval(() => {
                this.detectObj(net);
            }, 10);
        },
        drawRect(detections, ctx) {
            detections.forEach(prediction => {
                const [x,y,width,height] = prediction['bbox']
                const text = prediction['class'] + ' ' + (prediction['score']*100).toFixed(0) + '%'
                const color = 'blue'
                ctx.strokeStyle = color
                ctx.lineWidth = 2;
                ctx.font = '20px Arial'
                ctx.fillStyle = color
                ctx.beginPath()
                ctx.fillText(text, x, y-6)
                ctx.rect(x, y, width, height)
                ctx.stroke()
            });
        }
    }
  }
</script>

<style>

.btn2 {
    border-radius: 3px;
    background-color: green;
    color: white;
    font-size: medium;
}

.btn2:disabled {
    background-color: lightgray;
    border-radius: 3px;
    color: black;
    font-size: medium;
}

.video-container {
    position: relative;
}

canvas{
    position: absolute;
    z-index: 10;
    left: 0px;
    top: 0px;
}

video {
    background: url('../assets/overlay.png');
    background-position: center;
}

</style>